import { Component, OnInit, Input, ViewChild, ElementRef }      from '@angular/core';

// Models
import { Category }                                             from '../../models/category';
import { User }                                                 from '../../models/user';

// Custom services
import { UserService }                                          from '../../services/user.service';
import { CategoryService }                                      from '../../services/category.service';

@Component({
  selector: 'categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.css'],
  providers: [UserService, CategoryService]
})
export class CategoriesComponent implements OnInit {

    public title: String;
    public newCategory: Boolean;
    public newCategoryDescription: String;
    public category: Category;
    public categories: Category[];
    public inputError: Boolean;
    public errorMessage: String;
    public userId: String;

    @Input() userName: String;
    @ViewChild('newCategory', {static: false}) newCategoryComponent: ElementRef

    constructor(
        private _userService: UserService,
        private _categoryService: CategoryService
    ){
        this.title = 'Categoras';
        this.inputError = false;
     }

    ngOnInit() {
        this.getUserId(this.userName);
    }

    openNewCategory(){
        this.newCategory = true;
        setTimeout(() => {
            this.newCategoryDescription = '';
            this.newCategoryComponent.nativeElement.focus();
        }, 100);
    }

    closeNewCategory(){
        this.newCategory = false;
    }

    clearErrors(){
        this.inputError = false;
        this.errorMessage = '';
    }

    getCategories(){
        this._categoryService.getCategories(this.userId).subscribe(
            response => {
                if(response.categories){
                    this.categories = response.categories;
                }
            }
        )
    }

    getUserId(userName: String){
        this._userService.getUserByName(userName).subscribe(
            response => {
                if(response.user){
                    this.userId = response.user[0]._id;
                    this.getCategories();
                }
            },
            error => {
                console.log(<any>error);
            }
        )
    }

    /******************* MTODOS DE ESCRITURA EN LA BBDD *************/
    createCategory(description: String){
        this.category = new Category('', this.userId, description);
        this._categoryService.createCategory(this.category).subscribe(
            response => {
                if(response.category){
                    this.newCategory = false;
                    this.clearErrors();
                    this.getCategories();
                }
            },
            error => {
                console.log(<any>error);
            }
        )
    }

    deleteCategory(categoryId: String){
        if(confirm('Ests seguro de que quieres borrar definitivamente esta categora?')){
            this._categoryService.deleteCategory(categoryId).subscribe(
                response => {
                    if(response.category){
                        this.clearErrors();
                        this.getCategories();
                    }
                },
                error => {
                    console.log(<any>error);
                }
            );
        }
    }

    updateCategory(categoryId: String, name: String){
        if(name == ''){
            this.inputError = true;
            this.errorMessage = 'Introduce el nombre de la categora';
            return;
        }
        const category = new Category(categoryId, this.userId, name);
        this._categoryService.updateCategory(categoryId, category).subscribe(
            response => {
                if(response.category){
                    this.clearErrors();
                    this.getCategories();
                }
            },
            error => {
                console.log(<any>error);
            }
        )
    }

}
