import { Injectable, RootRenderer }     from '@angular/core';
import { HttpClient, HttpHeaders }      from '@angular/common/http';
import { Observable }                   from 'rxjs';

// Variables globales
import { Global }                       from './global.service';

// Model
import { Assignation }                  from '../models/assignation';

@Injectable({
    providedIn: 'root'
})
export class AssignationService{
    public url: String;
    public headers: HttpHeaders;

    constructor(private _http: HttpClient){
        this.url = Global.url;
        this.headers = new HttpHeaders().set('Content-Type', 'application/json');
    }

    getAssignations(userId: String): Observable<any>{
        // Montamos la url de parmetros para el filtro
        const url = this.url + 'assignations?userId=' + userId;
        return this._http.get(url, {headers: this.headers});
    }

    createAssignation(assignation: Assignation): Observable<any>{
        const params = JSON.stringify(assignation);
        return this._http.post(this.url + 'createAssignation', params, {headers: this.headers});
    }

    updateAssignation(id: String, assignation: Assignation): Observable<any>{
        return this._http.put(this.url + 'assignation/' + id, assignation, {headers: this.headers})
    }

    deleteAssignation(id: String): Observable<any>{
        return this._http.delete(this.url + 'assignation/' + id, {headers: this.headers});
    }
}
