import { Component, OnInit }        from '@angular/core';

// Servicios custom
import { UserService }              from '../../services/user.service';
import { User }                     from '../../models/user';

@Component({
  selector: 'register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
  providers: [UserService]
})
export class RegisterComponent implements OnInit {

    public title: String;
    public user: User;
    public okMessage: boolean;
    public errorMessage: boolean;
    public nameEmpty: boolean;
    public passwordEmpty: boolean;
    public message:String;

    constructor(
        private _userService: UserService
    )
    {
        this.title = 'Registro de usuario';
        this.user = new User('', '');
        this.okMessage = false;
        this.errorMessage = false;
        this.nameEmpty = false;
        this.passwordEmpty = false;
        this.message = '';
    }

    ngOnInit() {
    }

    onSubmit(event){
        event.preventDefault();
        // Primero miramos si existe el usuario
        this._userService.getUserByName(this.user.name).subscribe(
            response => {
                if(response.user.length){
                    this.message = 'Ya existe el usuario introducido';
                    this.okMessage = false;
                    this.errorMessage = true;
                }else{
                    this._userService.createUser(this.user).subscribe(
                        response => {
                            if(response.user){
                                console.log(response.user);
                                this.errorMessage = false;
                                this.okMessage = true;
                            }
                        },
                        error => {
                            console.log(<any>error);
                            this.message = 'Error al registrar el usuario';
                            this.errorMessage = true;
                            this.okMessage = false;
                        }
                    );
                }
            },
            error => {
                console.log(<any>error);
            }
        )
    }

    // Utilizaremos esta clase para validar que los campos no se dejen en blanco
    onBlurName(){
        this.errorMessage = false;
        this.okMessage = false;
        this.nameEmpty = this.user.name == '' ? true : false;
        console.log(this.errorMessage);
    }

    onFocusName(){
        this.nameEmpty = false;
    }

    onBlurPassword(){
        this.passwordEmpty = this.user.password == '' ? true : false;
        this.errorMessage = false;
        this.okMessage = false;
    }

    onFocusPassword(){
        this.passwordEmpty = false;
    }

}
