import { Component, OnInit, EventEmitter, Output }      from '@angular/core';

// Custom services
import { UserService }                                  from '../../services/user.service';

@Component({
    selector: 'menu',
    templateUrl: './menu.component.html',
    styleUrls: ['./menu.component.css'],
    providers: [UserService]
})
export class MenuComponent implements OnInit {

    @Output() getUsername = new EventEmitter();
    @Output() getMenuOption = new EventEmitter();

    public loggedIn: Boolean;
    public loggedUser: String;
    public menuOption: String;
    public currentOption: String;

    constructor(
        private _userService: UserService
    ){
        this.loggedIn = this._userService.isLoggedIn();
    }

    ngOnInit(){
        this.loggedUser = this._userService.getUserLoggedIn();
        this.getUsername.emit(this.loggedUser);
        this.menuOption = 'I';
        this.currentOption = this.menuOption;
        this.getMenuOption.emit(this.menuOption);
    }

    showImportants(){
        this.menuOption = 'I';
        this.currentOption = this.menuOption;
        this.getMenuOption.emit(this.menuOption);
    }

    showSecondary(){
        this.menuOption = 'S';
        this.currentOption = this.menuOption;
        this.getMenuOption.emit(this.menuOption);
    }

    showByCategory(){
        this.menuOption = 'BC';
        this.currentOption = this.menuOption;
        this.getMenuOption.emit(this.menuOption);
    }

    showCategories(){
        this.menuOption = 'C';
        this.currentOption = this.menuOption;
        this.getMenuOption.emit(this.menuOption);
    }

    showAssignations(){
        this.menuOption = 'A';
        this.currentOption = this.menuOption;
        this.getMenuOption.emit(this.menuOption);
    }

    showAssignatedTasks(){
        this.menuOption = 'AT';
        this.currentOption = this.menuOption;
        this.getMenuOption.emit(this.menuOption);
    }

    showHistory(){
        this.menuOption = 'H';
        this.currentOption = this.menuOption;
        this.getMenuOption.emit(this.menuOption);
    }

}
