import { Component, OnInit, Input, ViewChild, ElementRef }      from '@angular/core';

// Models
import { Assignation }                                          from '../../models/assignation';
import { User }                                                 from '../../models/user';

// Custom services
import { UserService }                                          from '../../services/user.service';
import { AssignationService }                                   from '../../services/assignation.service';

@Component({
  selector: 'assignations',
  templateUrl: './assignations.component.html',
  styleUrls: ['./assignations.component.css'],
  providers: [UserService, AssignationService]
})
export class AssignationsComponent implements OnInit {

    public title: String;
    public newAssignation: Boolean;
    public newAssignationDescription: String;
    public assignation: Assignation;
    public assignations: Assignation[];
    public inputError: Boolean;
    public errorMessage: String;
    public userId: String;

    @Input() userName: String;
    @ViewChild('newAssignation', {static: false}) newAssignationComponent: ElementRef

    constructor(
        private _userService: UserService,
        private _assignationService: AssignationService
    ){
        this.title = 'Asignaciones';
        this.inputError = false;
     }

    ngOnInit() {
        this.getUserId(this.userName);
    }

    openNewAssignation(){
        this.newAssignation = true;
        setTimeout(() => {
            this.newAssignationDescription = '';
            this.newAssignationComponent.nativeElement.focus();
        }, 100);
    }

    closeNewAssignation(){
        this.newAssignation = false;
    }

    clearErrors(){
        this.inputError = false;
        this.errorMessage = '';
    }

    getAssignations(){
        this._assignationService.getAssignations(this.userId).subscribe(
            response => {
                if(response.assignations){
                    this.assignations = response.assignations;
                }
            }
        )
    }

    getUserId(userName: String){
        this._userService.getUserByName(userName).subscribe(
            response => {
                if(response.user){
                    this.userId = response.user[0]._id;
                    this.getAssignations();
                }
            },
            error => {
                console.log(<any>error);
            }
        )
    }

    /******************* MTODOS DE ESCRITURA EN LA BBDD *************/
    createAssignation(description: String){
        this.assignation = new Assignation('', this.userId, description);
        this._assignationService.createAssignation(this.assignation).subscribe(
            response => {
                if(response.assignation){
                    this.newAssignation = false;
                    this.clearErrors();
                    this.getAssignations();
                }
            },
            error => {
                console.log(<any>error);
            }
        )
    }

    deleteAssignation(assignationId: String){
        if(confirm('Ests seguro de que quieres borrar definitivamente esta asignacin?')){
            this._assignationService.deleteAssignation(assignationId).subscribe(
                response => {
                    if(response.assignation){
                        this.clearErrors();
                        this.getAssignations();
                    }
                },
                error => {
                    console.log(<any>error);
                }
            );
        }
    }

    updateAssignation(assignationId: String, name: String){
        if(name == ''){
            this.inputError = true;
            this.errorMessage = 'Introduce el nombre de la persona asignada';
            return;
        }
        const assignation = new Assignation(assignationId, this.userId, name);
        this._assignationService.updateAssignation(assignationId, assignation).subscribe(
            response => {
                if(response.category){
                    this.clearErrors();
                    this.getAssignations();
                }
            },
            error => {
                console.log(<any>error);
            }
        )
    }

}
