import { Injectable }               from '@angular/core';
import { HttpClient, HttpHeaders}   from '@angular/common/http';
import { Observable }               from 'rxjs';

// Variables globales
import { Global }                   from './global.service';

// Model
import { User }                     from '../models/user';

@Injectable({
    providedIn: 'root'
})
export class UserService {

    private url: String;
    private headers: HttpHeaders;
    private loggedIn: Boolean;

    constructor(private _http: HttpClient){
        this.url = Global.url;
        this.headers = new HttpHeaders().set('Content-Type','application/json');
        this.loggedIn = false;
    }

    createUser(user: User): Observable<any>{
        const params = JSON.stringify(user);
        return this._http.post(this.url + 'saveUser',  params, {headers: this.headers});
    }

    getUserByName(name: String): Observable<any>{
        return this._http.get(this.url + 'userByName/' + name, {headers: this.headers});
    }

    authenticate(user: User): Observable<any>{
        const params = JSON.stringify(user);
        return this._http.post(this.url + 'authentication', params, {headers: this.headers});
    }

    setUserLoggedIn(username: String){
        localStorage.setItem('currentUser', JSON.stringify(username));
        this.loggedIn = true;
    }

    getUserLoggedIn(){
        return JSON.parse(localStorage.getItem('currentUser'));
    }

    isLoggedIn(){
        return this.loggedIn;
    }
}
