import { Component, OnInit }            from '@angular/core';
import { Router }                       from '@angular/router';

// Servicios custom
import { UserService }              from '../../services/user.service';
import { User }                     from '../../models/user';

@Component({
    selector: 'login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css'],
    providers: [UserService]
})
export class LoginComponent implements OnInit {

    public title: String;
    public user: User;
    public okMessage: boolean;
    public errorMessage: boolean;
    public nameEmpty: boolean;
    public passwordEmpty: boolean;
    public message:String;

    constructor(
        private _userService: UserService,
        private _router: Router
    ){
        this.title = 'Inicio de Sesin'
        this.user = new User('','');
        this.okMessage = false;
        this.errorMessage = false;
        this.nameEmpty = false;
        this.passwordEmpty = false;
        this.message = '';
     }

    ngOnInit() {
    }

    onSubmit(event){
        // Comprobamos que el ususario exista
        this._userService.getUserByName(this.user.name).subscribe(
            response => {
                if(response.user.length){
                    // Si existe, comparamos la contrasea introducida con la guardada en la base de datos
                    this._userService.authenticate(this.user).subscribe(
                        response => {
                            this._userService.setUserLoggedIn(this.user.name);
                            this._router.navigateByUrl('/main');
                        },
                        error => {
                            if(error.status == 403){
                                this.message = 'La constrasea no coincide con la del usuario registrado';
                                this.okMessage = false;
                                this.errorMessage = true;
                            }
                        }
                    )
                }else{
                    this.message = 'Usuario inexistente';
                    this.okMessage = false;
                    this.errorMessage = true;
                }
            }
        );
    }

    onBlurName(){
        this.errorMessage = false;
        this.okMessage = false;
        this.nameEmpty = this.user.name == '' ? true : false;
    }

    onFocusName(){
        this.nameEmpty = false;
    }

    onBlurPassword(){
        this.passwordEmpty = this.user.password == '' ? true : false;
        this.errorMessage = false;
        this.okMessage = false;
    }

    onFocusPassword(){
        this.passwordEmpty = false;
    }

}
