import { Injectable, RootRenderer }     from '@angular/core';
import { HttpClient, HttpHeaders }      from '@angular/common/http';
import { Observable }                   from 'rxjs';

// Variables globales
import { Global }                       from './global.service';

// Model
import { Category }                     from '../models/category';

@Injectable({
    providedIn: 'root'
})
export class CategoryService{
    public url: String;
    public headers: HttpHeaders;

    constructor(private _http: HttpClient){
        this.url = Global.url;
        this.headers = new HttpHeaders().set('Content-Type', 'application/json');
    }

    getCategories(userId: String): Observable<any>{
        // Montamos la url de parmetros para el filtro
        const url = this.url + 'categories?userId=' + userId;
        return this._http.get(url, {headers: this.headers});
    }

    createCategory(category: Category): Observable<any>{
        const params = JSON.stringify(category);
        return this._http.post(this.url + 'createCategory', params, {headers: this.headers});
    }

    updateCategory(id: String, category: Category): Observable<any>{
        return this._http.put(this.url + 'category/' + id, category, {headers: this.headers})
    }

    deleteCategory(id: String): Observable<any>{
        return this._http.delete(this.url + 'category/' + id, {headers: this.headers});
    }
}
