import { Component, OnInit, Input }         from '@angular/core';

// Models
import { User }                             from '../../models/user';
import { Assignation }                      from '../../models/assignation';
import { Task }                             from '../../models/task';
import { Category }                         from '../../models/category';

// Custom services
import { AssignationService }               from '../../services/assignation.service';
import { TaskService }                      from '../../services/task.service';
import { UserService }                      from '../../services/user.service';
import { CategoryService }                  from '../../services/category.service';

@Component({
    selector: 'assigned-tasks',
    templateUrl: './assigned-tasks.component.html',
    styleUrls: ['./assigned-tasks.component.css'],
    providers: [AssignationService, TaskService, UserService, CategoryService]
})
export class AssignedTasksComponent implements OnInit {

    @Input() userName: String;

    public title: String;
    public assignations: Assignation[];
    public tasks: Task[];
    public task: Task;
    public categories: Category[];
    public userId: String;
    public currentAssignationId: String;

    constructor(
        private _assignationService: AssignationService,
        private _taskService: TaskService,
        private _userService: UserService,
        private _categoryService: CategoryService
    ){
        this.title = 'Tareas asignadas';
        this.tasks = [];
        this.categories = [];
        this.currentAssignationId = '';
     }

    ngOnInit() {
        this.getUserId(this.userName);
    }

    getAssignations(): void{
        this._assignationService.getAssignations(this.userId).subscribe(
            response => {
                if(response.assignations){
                    this.assignations = response.assignations;
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }

    getUserId(userName: String): void{
        this._userService.getUserByName(userName).subscribe(
            response => {
                if(response.user){
                    this.userId = response.user[0]._id;
                    this.getAssignations();
                    this.getCategories();
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }

    getCategories(): void{
        this._categoryService.getCategories(this.userId).subscribe(
            response => {
                if(response.categories){
                    this.categories = response.categories;
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }

    mapCategory(categoryId: String): String{
        let categoryName = '';
        this.categories.forEach(category => {
            if(category._id == categoryId) categoryName = <string>category.name;
        });
        return categoryName;
    }

    loadTasks(assignationId: String){
        if(this.currentAssignationId == '') this.currentAssignationId = assignationId;
        else                                this.currentAssignationId = '';

        this._taskService.getAllTasksByAssignation(assignationId, this.userId).subscribe(
            response => {
                if(response.tasks){
                    this.tasks = response.tasks;
                }
            },
            error => {
                console.log(<any>error);
            }
        );
    }

    completeTask(taskId: String, description: String, date: String, categoryId: String, assignationId: String): void{
        if(confirm('Ests seguro de que quieres marcar como completada esta tarea?')){
            this.task = new Task(taskId,
                                    this.userId,
                                    description,
                                    date,
                                    'C',
                                    false,
                                    categoryId,
                                    assignationId);
            this._taskService.updateTask(taskId, this.task).subscribe(
                response => {
                    if(response.task){
                        this.loadTasks(assignationId);
                    }
                },
                error => {
                    console.log(<any>error);
                }
            );
        }
    }

}
