import { Pipe, PipeTransform }				from '@angular/core';

@Pipe({
	name: 'taskOrder'
})
export class TaskOrderPipe implements PipeTransform{
	transform(value: any[]): any[]{
        let column: String;

		if(value.length == 1) return value;

		const sortedValue = this.orderBy(value);
		return sortedValue;
	}

	orderBy(value: String[]){
        let changes = false;
        let result;
		const sortedArray: String[] = value.sort((a: any, b: any): number =>{

            const yearA = a['date'].substring(6,10);
            const monthA = a['date'].substring(3,5);
            const dayA = a['date'].substring(0,2);

            const yearB = b['date'].substring(6,10);
            const monthB = b['date'].substring(3,5);
            const dayB = b['date'].substring(0,2);

            const dateA = yearA + monthA + dayA;
            const dateB = yearB + monthB + dayB;

            const dateAInt = parseInt(dateA);
            const dateBInt = parseInt(dateB);

            if(dateAInt > dateBInt) result = 1;
            if(dateAInt < dateBInt) result = -1;
            if(dateAInt == dateBInt) result = 0;

            return result;
		});

        return sortedArray.reverse();
	}
}
