import { Component, OnInit, ViewChild,
    ElementRef, Input }                                 from '@angular/core';

// Models
import { Task }                                         from '../../models/task';
import { Category }                                     from '../../models/category';
import { Assignation }                                  from '../../models/assignation';

// Custom services
import { UserService }                                  from '../../services/user.service';
import { TaskService }                                  from '../../services/task.service';
import { CategoryService }                              from '../../services/category.service';
import { AssignationService }                           from '../../services/assignation.service';

@Component({
selector: 'secondary',
templateUrl: './secondary.component.html',
styleUrls: ['./secondary.component.css'],
providers: [TaskService, CategoryService, AssignationService, UserService]
})
export class SecondaryComponent implements OnInit {

public newTask: boolean;
public tasks: Task[];
public task: Task;
public categories: Category[];
public assignations: Assignation[];
public taskCategory: String;
public title: String;
public newTaskDate: String;
public newTaskDescription: String;
public taskAssigned: boolean;
public _assignationId: String;
public userId: String;
public errorMessage: String;
public inputError: boolean;

@Input() userName: String;
@ViewChild('newTask', {static: false}) newTaskComponent: ElementRef;

constructor(
   private _taskService: TaskService,
   private _categoryService: CategoryService,
   private _assignationService: AssignationService,
   private _userService: UserService
){
   this.title = 'Tareas Secundarias';
   this.newTask = false;
   this.categories = [];
   this.tasks = [];
   this.taskAssigned = false;
   this.inputError = false;
}

ngOnInit() {
   this.getUserId(this.userName);
}

openNewTask(): void{
   this.newTask = true;
   this.taskCategory = '';
   this.newTaskDate = '';
   this.newTaskDescription = '';
   /* Utilizamos el setTimeout para dar tiempo a que se cargue el componente
      html. Si lo hacemos inmediatamente, al estar sujeto al *ngIf, dar
      nulo
   */
   setTimeout(() => {
       this.newTaskComponent.nativeElement.innerHTML = '';
       this.newTaskComponent.nativeElement.focus();
   }, 100);
}

closeNewTask(): void{
   this.newTask = false;
}

getTasks(): void{
   this._taskService.getSecondaryTasks(this.userId).subscribe(
       response => {
           if(response.tasks){
               this.tasks = response.tasks;
           }
       },
       error => {
           console.log(<any>error);
       }
   );
}

getCategories(): void{
   this._categoryService.getCategories(this.userId).subscribe(
       response => {
           if(response.categories){
               this.categories = response.categories;
           }
       },
       error => {
           console.log(<any>error);
       }
   );
}

getAssignations(): void{
   this._assignationService.getAssignations(this.userId).subscribe(
       response => {
           if(response.assignations){
               this.assignations = response.assignations;
           }
       },
       error => {
           console.log(<any>error);
       }
   );
}

getUserId(userName: String): void{
   this._userService.getUserByName(userName).subscribe(
       response => {
           if(response.user){
               this.userId = response.user[0]._id;
               this.getCategories();
               this.getAssignations();
               this.getTasks();
           }
       },
       error => {
           console.log(<any>error);
       }
   );
}

openAssignment(taskId: String, description: String, date: String, categoryId: String): void{
   if(description == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la descripcin de la tarea';
       return;
   }
   if(date == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la fecha de la tarea';
       return;
   }
   if(categoryId == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la categora de la tarea';
       return;
   }
   this.taskAssigned = true;
   this.task = new Task(taskId,
                        this.userId,
                        description,
                        date,
                        'P',
                        true,
                        categoryId,
                        'none');
}

closeAssignment(): void{
   this.taskAssigned = false;
}

clearErrors(){
   this.inputError = false;
   this.errorMessage = '';
}

/******************* MTODOS DE ESCRITURA EN LA BBDD *************/
createTask(description: String, date: String, categoryId: String): void{
   if(description == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la descripcin de la tarea';
       return;
   }
   if(date == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la fecha de la tarea';
       return;
   }
   if(categoryId == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la categora de la tarea';
       return;
   }
   this.task = new Task('',
                        this.userId,
                        description,
                        date,
                        'P',
                        false,
                        categoryId,
                        'none');
   this._taskService.createTask(this.task).subscribe(
       response => {
           if(response.task){
               this.newTask = false;
               this.getTasks();
               this.clearErrors();
           }
       },
       error => {
           console.log(<any>error);
           this.inputError = true;
           this.errorMessage = 'Error al crear la tarea';
       }
   );
}

upgradeTask(taskId: String, description: String, date: String, categoryId: String): void{
   if(description == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la descripcin de la tarea';
       return;
   }
   if(date == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la fecha de la tarea';
       return;
   }
   if(categoryId == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la categora de la tarea';
       return;
   }
   this.task = new Task(taskId,
                        this.userId,
                        description,
                        date,
                        'P',
                        true,
                        categoryId,
                        'none');
    this._taskService.updateTask(taskId, this.task).subscribe(
       response => {
           if(response.task){
               this.getTasks();
               this.clearErrors();
           }
       },
       error => {
           this.inputError = true;
           this.errorMessage = 'Error al modificar la tarea';
           console.log(<any>error);
       }
   );
}

updateTask(taskId: String, description: String, date: String, categoryId: String): void{
   // Validaciones
   if(description == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la descripcin de la tarea';
       return;
   }
   if(date == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la fecha de la tarea';
       return;
   }
   if(categoryId == ''){
       this.inputError = true;
       this.errorMessage = 'Introduce la categora de la tarea';
       return;
   }
   this.task = new Task(taskId,
                        this.userId,
                        description,
                        date,
                        'P',
                        false,
                        categoryId,
                        'none');
   this._taskService.updateTask(taskId, this.task).subscribe(
       response => {
           if(response.task){
               this.getTasks();
               this.clearErrors();
           }
       },
       error => {
           this.inputError = true;
           this.errorMessage = 'Error al modificar la tarea';
           console.log(<any>error);
       }
   );
}

completeTask(taskId: String, description: String, date: String, categoryId: String): void{
    if(confirm('Ests seguro de que quieres marcar como completada esta tarea?')){
        if(description == ''){
            this.inputError = true;
            this.errorMessage = 'Introduce la descripcin de la tarea';
            return;
        }
        if(date == ''){
            this.inputError = true;
            this.errorMessage = 'Introduce la fecha de la tarea';
            return;
        }
        if(categoryId == ''){
            this.inputError = true;
            this.errorMessage = 'Introduce la categora de la tarea';
            return;
        }
        this.task = new Task(taskId,
                                this.userId,
                                description,
                                date,
                                'C',
                                false,
                                categoryId,
                                'none');
        this._taskService.updateTask(taskId, this.task).subscribe(
            response => {
                if(response.task){
                    this.getTasks();
                    this.clearErrors();
                }
            },
            error => {
                this.inputError = true;
                this.errorMessage = 'Error al completar la tarea';
                console.log(<any>error);
            }
        );
    }
}

assignTask(): void{
    if(this.task && this._assignationId){
        this.task.assignationId = this._assignationId;
        console.log(this.task);
        this._taskService.updateTask(this.task._id, this.task).subscribe(
            response => {
                if(response.task){
                    this.getTasks();
                    this.taskAssigned = false;
                    this.clearErrors();
                }
            },
            error => {
                console.log(<any>error);
                this.inputError = true;
                this.errorMessage = 'Error al asignar la tarea';
            }
        );
    }
}

deleteTask(taskId: String): void{
    if(confirm('Ests seguro de que quieres borrar definitivamente esta tarea?')){
        this._taskService.deleteTask(taskId).subscribe(
            response => {
                if(response.task){
                    this.getTasks();
                    this.clearErrors();
                }
            },
            error => {
                console.log(<any>error);
                this.inputError = true;
                this.errorMessage = 'Error al eliminar la tarea';
            });
        }
    }
}
