import { Injectable, RootRenderer }     from '@angular/core';
import { HttpClient, HttpHeaders }      from '@angular/common/http';
import { Observable }                   from 'rxjs';

// Variables globales
import { Global }                       from './global.service';

// Model
import { Task }                         from '../models/task';

@Injectable({
    providedIn: 'root'
})
export class TaskService{
    public url: String;
    public headers: HttpHeaders;

    constructor(private _http: HttpClient){
        this.url = Global.url;
        this.headers = new HttpHeaders().set('Content-Type', 'application/json');
    }

    getTasks(userId: String): Observable<any>{
        // Montamos la url de parmetros para el filtro
        const url = this.url + 'tasks?status=C&userId=' + userId;
        return this._http.get(url, {headers: this.headers});
    }

    getImportantTasks(userId: String): Observable<any>{
        // Montamos la url de parmetros para el filtro
        const url = this.url + 'tasks?important=true&status=P&assignationId=none&userId=' + userId;
        return this._http.get(url, {headers: this.headers});
    }

    getAllTasksByCategory(categoryId: String, userId: String): Observable<any>{
        const url = this.url + 'tasks?status=P&assignationId=none&userId=' + userId + '&categoryId=' + categoryId;
        return this._http.get(url, {headers: this.headers});
    }

    getAllTasksByAssignation(assignationId: String, userId: String): Observable<any>{
        const url = this.url + 'tasks?status=P&assignationId=' + assignationId + '&userId=' + userId;
        return this._http.get(url, {headers: this.headers});
    }

    getSecondaryTasks(userId: String): Observable<any>{
        // Montamos la url de parmetros para el filtro
        const url = this.url + 'tasks?important=false&status=P&assignationId=none'
        return this._http.get(url, {headers: this.headers});
    }

    createTask(task: Task): Observable<any>{
        const params = JSON.stringify(task);
        return this._http.post(this.url + 'createTask', params, {headers: this.headers});
    }

    updateTask(id: String, task: Task): Observable<any>{
        const params = JSON.stringify(task);
        return this._http.put(this.url + 'task/' + id, params, {headers: this.headers});
    }

    deleteTask(id: String): Observable<any>{
        return this._http.delete(this.url + 'task/' + id, {headers: this.headers});
    }

}
