import { Component, OnInit, ViewChild, ElementRef }         from '@angular/core';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {

    public newTask: boolean;
    public userName: String;
    public menuOption: string;

    @ViewChild('newTask', {static: false}) newTaskComponent: ElementRef;

    constructor(){
        this.newTask = false;
    }

    ngOnInit() {
    }

    openNewTask(){
        this.newTask = true;
        /* Utilizamos el setTimeout para dar tiempo a que se cargue el componente
           html. Si lo hacemos inmediatamente, al estar sujeto al *ngIf, dar
           nulo
        */
        setTimeout(() => {
            this.newTaskComponent.nativeElement.focus();
        }, 100);
    }

    getUsername(event){
        this.userName = event;
    }

    getMenuOption(event){
        this.menuOption = event;
    }
}
